import request from '@/utils/request'

export function getBanner(channel) {//获取首页轮播图
  const data={
    channel
  }
  return request({
    url:'/index/banner',
    method:'post',
    data
  })
}

export function getTopic() {//获取首页推荐主题
  return request({
    url:'/index/topic',
    method:'post',
  })
}
export function getSort() {//获取首页分类
  return request({
    url:'/index/class',
    method:'post',
  })
}
export function getCateRecipe() {//获取首页精选分类菜谱
  return request({
    url:'/index/categoryrecipe',
    method:'post',
  })
}
export function getRecipe(page) {//获取首页精选菜谱
  const data={
    page
  }
  return request({
    url:'/index/featuredrecipe',
    method:'post',
    data
  })
}



