<template>
  <div>
    <!--    头部-->
    <div id="header" :class="showbg?'navchange':'none'">
      <div>
        <div class="header clearfix">
          <a class="logo left" href="/"></a>
          <ul class="nav">
            <li class="act"><a>首页</a></li>
            <li @click="changeTab(2)" :class="activeName==2?'act':''">
              <router-link to="/sortList?activeName=2">菜谱分类 <span class="naww"></span></router-link>
            </li>
            <li @click="changeTab(3)" :class="activeName==3?'act':''">
              <router-link to="/themeList?activeName=3">美食主题</router-link>
            </li>
            <li @click="changeTab(4)" :class="activeName==4?'act':''">
              <router-link to="/dTList?activeName=4">饭圈</router-link>
            </li>
            <li>
              <el-dropdown>
                <span class="el-dropdown-link qr-box-con">
                  APP下载<i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown" class="qr-box">
                  <div class="erweima">
                    <a href="https://guanfan.oss-cn-hangzhou.aliyuncs.com/apk/guanfan.apk">
                    <img src="../../static/guanfan_android.png" style="width:100%;"/>
                    <p class="whiteColor text-12">安卓APP</p>
                    </a>
                  </div>
                  <div class="erweima">
                    <a href="https://apps.apple.com/cn/app/id1487127641">
                    <img src="../../static/guanfan_ios.png" style="width:100%;"/>
                    <p class="whiteColor text-12">苹果APP</p>
                    </a>
                  </div>

                </el-dropdown-menu>
              </el-dropdown>
            </li>
          </ul>
          <form class="search br4 left" id="searchForm"  accept-charset="utf-8">
            <input type="text" id="global_search_inpt" class="sput"
                   placeholder="搜索菜谱、食材、用户" lang="zh-CN"
                   autocomplete="off"
                  v-model="searchkey"
                   @keydown.enter="goSearch"/>
                  <div class="lib" @click="goSearch"><i class="iconfont">&#xe667;</i></div>
            <div class="sugg2" id="mysu2" style="display:none;"></div>
          </form>
          <div class="pubt">
            <el-dropdown>
              <el-button type="success" class="btn-pubt">
                发布<i class="el-icon-edit"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>
                  <router-link to="/publish">食谱</router-link>
                </el-dropdown-item>
                <el-dropdown-item>
                  <router-link to="/publishDt">动态</router-link>
                </el-dropdown-item>
                <el-dropdown-item>
                  <router-link to="/publishtheme">主题</router-link>
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
          <div v-if="userInfo" class="userinfo">
            <router-link to="/user">
              <img :src="osshost + userInfo.img" v-if="userInfo.img"/>
              <span class="userinfospan">{{ userInfo.name }}</span>
            </router-link>
            <el-button type="danger" round size="mini" @click="loginOut">退出</el-button>
          </div>
          <div v-else class="perinfo">
            <router-link to="/login"> 登录</router-link>
            <span>/ </span>
            <router-link to="/signup"> 注册</router-link>
          </div>
        </div>
      </div>
    </div>
    <div ref="scrollList">
      <el-carousel height="560px">
        <el-carousel-item v-for="(item,index) in hbimg" :key="index" align="center">
          <img class="hbimg" v-if="item.type==2" :src="osshost+item.bannerimg" @click="bookDetail(item.recipeid)"/>
          <a v-if="item.type==1" :href="item.linkurl"><img class="hbimg" :src="osshost+item.bannerimg"/></a>
        </el-carousel-item>
      </el-carousel>
      <div id="content" class="clearfix content">
        <!--    今日主题-->
        <h2 class="title">今日主题
          <router-link to="/themeList?activeName=3" class="more">更多<i class="iconfont mainColor">&#xe654;</i></router-link>
        </h2>
        <ul class="theme-list clearfix">
          <li class="item" v-for="(item,index) in themeList.slice(0,3)" :key="index"
              v-loading="loading">
            <div class="cover bg-img xl" @click="navToDetail(item.topicid)"
                 :style="{ backgroundImage: 'url(' + osshost + item.topicimg +detailpic+ ')' }">
            </div>
            <div class="bg-sd">
              <div class="name text-tips text-cut">{{ item.title }}</div>
              <div class="author">
                <img :src="osshost + item.faceimg + userface" class="headicon br50">
                <span class="nickname text-lips" style="max-width: 150px">{{ item.nickname }}</span>
                <span class="right text-xm el-icon-star-off margin-right-xs block">{{ item.collections }}</span>
              </div>
            </div>
          </li>
        </ul>

        <!--  早午晚-->
        <ul class="like-list4 clearfix">
          <div class="like-list-box4" v-for="(item, index) in menuClassList" :key="index" v-loading="loading">
            <div class="like-list-name text-25 text-bold mt20">{{ item.name }}</div>
            <!-- 分类列表内容 -->
            <li class="item" v-for="(childitem, childindex) in item.cplist" :key="childitem.cpid">
              <div class="item-box">
              <div class="cover bg-img" @click="bookDetail(childitem.cpid)"
                   :style="{ backgroundImage: 'url(' + osshost +childitem.cpimg +listpic+ ')' }">
              </div>
              <div>
                <div class="name text-tips text-cut">{{ childitem.cptitle }}</div>
                <div class="author">
                  <img :src="osshost + childitem.faceimg +userface" class="headicon br50"
                       @click="navToIndex(childitem.userid)">
                  <span class="nickname text-lips" style="max-width: 238px">{{childitem.nickname}}</span>
                  <span class="right text-xm el-icon-star-off margin-right-xs block">{{ childitem.collections }}</span>
                </div>
              </div>
              </div>
            </li>

          </div>
          <!-- 分类列表内容 -->
        </ul>
      </div>

      <div
        style="max-width:100%;height:300px;margin:20px 0;background: url('https://guanfan.oss-cn-hangzhou.aliyuncs.com/apk/spiltImg.jpg') center no-repeat;background-size: cover"></div>

      <div class="clearfix content">
        <!--  最热-->
        <div class="ul_title">
          <div class="clearfix">
            <h2 class="title">精选食谱
            </h2>
          </div>
        </div>
        <ul class="like-list3 clearfix">
          <li class="item" v-for="(item, index) in menuList" :key="index">
            <div class="cover bg-img" :style="{ backgroundImage: 'url(' + osshost +item.cpimg +listpic+ ')' }"
                 @click="bookDetail(item.cpid)">
            </div>
            <div>
              <div class="name text-tips text-cut">{{ item.cptitle }}</div>
              <div class="author">
                <img :src="osshost + item.faceimg +userface" class="headicon br50" @click="navToIndex(item.userid)">
                <span class="nickname text-lips" style="max-width: 238px">{{item.nickname}}</span>
                <span class="right text-xm el-icon-star-off margin-right-xs block">{{ item.collections }}</span>
              </div>
            </div>
          </li>
        </ul>
        <infinite-loading @infinite="infiniteHandler" class="mt50 mb60">
          <div slot="spinner"><i class="el-icon-loading"></i>小弟拼命加载中...</div>//加载中的文字
          <div slot="no-more" class="el-icon-food grayColor">已加载完毕!</div>//加载完毕的文字
          <div slot="no-results" class="el-icon-coffee-cup grayColor">暂无数据:(</div>//没有数据的文字
        </infinite-loading>
      </div>
    </div>

    <!--    底部-->
    <FooterBottom></FooterBottom>
    <Totop></Totop>
  </div>
</template>

<script>
  import headerNav from "@/components/headerNav";
  import FoodItem from "@/components/foodItem.vue";
  import FooterBottom from "@/components/footer.vue";
  import Totop from '@/components/totop.vue';
  import InfiniteLoading from 'vue-infinite-loading';
  import {getMyInfo} from '@/api/user'
  import {getCateRecipe, getRecipe, getTopic, getBanner} from '@/api/main'
  import {Message} from "element-ui";

  export default {
    name: "index",
    inject: ['reload'],
    components: {
      FoodItem,
      headerNav,
      FooterBottom,
      InfiniteLoading,
      Totop
    },
    data() {
      return {
        loading: true,
        activeName: 1,
        activeIndex: '1',
        page: 1,
        userinfo: {},
        listpic: '?x-oss-process=style/list-img',
        userface: '?x-oss-process=style/user-img',
        detailpic: '?x-oss-process=style/detail-img',
        osshost: process.env.UPLOAD_URL,
        themeList: [],
        menuClassList: [],
        menuList: [],
        userInfo:{},
        searchkey:'',
        channel:1,
        hbimg: [],
        token: "",
        scrollTop:0,

      }
    },
    computed:{
          showbg: function(){
            let value = this.scrollTop>200?true:false;
            return value;
          },
        },
    created() {
      if (this.token != "") {
        this.getUser();
      }
      this.getTheme();
      this.getClass();
      this.getMenu();
      this.getBaner();
      window.addEventListener('scroll', this.getScrollTop);
    },
    mounted(){
      this.userInfo= this.$store.state.user.user_info;
      this.userInfo=JSON.parse(this.userInfo)
      this.token=this.$store.state.user.token;
    },
    methods: {
      getScrollTop() {
        this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      },
      changeTab(key) {
        this.activeName = key;
      },
      goSearch(){
        if(this.nav=="search"){
          this.reload()
        }else{
        }
        this.$router.push({path: "/search", query: {keyword:this.searchkey}})
        this.reload()
      },
      loginOut() {
        this.$store.dispatch('LogOut').then(() => {
          this.reload()
        }).catch(error => {
        })
      },
      getBaner() {
        getBanner(this.channel).then(response => {
          let res = response.data;
          if (res.success) {
            this.hbimg = res.result;
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      getUser() {
        getMyInfo().then(response => {
          let res = response.data;
          if (res.success) {
            this.userinfo = res.result;
            this.getUserInfo();
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      getTheme() {//获取主题
        getTopic().then(response => {
          let res = response.data;
          if (res.success) {
            this.themeList = res.result;
            this.loading = false
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      getClass() {//获取精选分类菜谱
        getCateRecipe().then(response => {
          if (response.data.success) {
            this.menuClassList = response.data.result;
            this.loading = false
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
            return;
          }
        }).catch(error => {
          console.log(error)
        })
      },
      infiniteHandler($state) {
        this.page++;
        this.getMenu($state);
      },
      getMenu($state) {//获取精选菜谱
        getRecipe(this.page).then(response => {
          if (response.data.success) {
            if (response.data.result.length != 0) {
              this.loading = false;
              $state.loaded();
              this.menuList = this.menuList.concat(response.data.result); //追加新数据
            } if(this.page==9) {
              this.loading = false;
              $state.complete(); //这里是加载了10页数据，设置不在加载
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
            return;
          }
        }).catch(error => {
          console.log(error)
        })
      },
      navToIndex(id) {
        this.$router.push({path: "/user", query: {id}})
      },
      navToDetail(id) {
        this.$router.push({path: "/themeDetail", query: {id: id}})
      },
      bookDetail(id) {
        this.$router.push({path: "/cookBook", query: {id: id}})
      },
    }
  }
</script>

<style scoped>
  #header {
    background: rgba(0, 0, 0, .0);
    box-shadow: none;
  }
.el-dropdown .qr-box-con{
  color: #fff;
}
  #header.navchange {
    background: rgba(255, 255, 255, 1);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.08);
  }

  #header .nav li {
    color: #fff;
  }

  #header .nav li > a {
    color: #fff;
  }

  #header .perinfo {
    color: #fff;
  }

  #header .perinfo a {
    color: #fff;
  }

  #header .perinfo a:hover {
    color: #FFB31A;
    font-weight: bold;
    border-bottom: 2px solid #FFB31A;
  }

  #header .logo {
    background: url('/static/logo.png') no-repeat center center;
    background-size: cover;
    display: block;
  }

  #header .userinfo span {
    color: #fff;
  }

  #header.navchange .nav li {
    color: #000;
  }
  #header.navchange .nav .el-dropdown .qr-box-con {
    color: #000;
  }

  #header.navchange .nav li > a {
    color: #000;
  }

  #header.navchange .perinfo {
    color: #000;
  }

  #header.navchange .perinfo a {
    color: #000;
  }

  #header.navchange .perinfo a:hover {
    color: #FFB31A;
    font-weight: bold;
    border-bottom: 2px solid #FFB31A;
  }

  #header.navchange .logo {
    background: url('/static/logo2.png') no-repeat center center;
    background-size: cover;
  }

  #header.navchange .userinfo span {
    color: #000;
  }
</style>



