import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/view/index'

import vueWaterfall from 'vue-waterfall2'
Vue.use(vueWaterfall)

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/dTList',
      component: () => import('@/view/dTList'),
    },
    {
      path: '/dtDetail',
      component: () => import('@/view/dtDetail'),
    },
    {
      path: '/cookBook',
      component: () => import('@/view/cookBook'),
    },{
      path: '/themeList',
      component: () => import('@/view/themeList'),
    },{
      path: '/themeDetail',
      component: () => import('@/view/themeDetail'),
    },
    {
      path: '/sortList',
      component: () => import('@/view/sort'),
    },
    {
      path: '/publish',
      component: () => import('@/view/publish')
    },
    {
      path: '/publishDt',
      component: () => import('@/view/publishDt')
    },
    {
      path: '/publishtheme',
      component: () => import('@/view/publishtheme')
    },
    {
      path: '/userinfo',
      component: () => import('@/view/userInfo')
    },
    {
      path: '/user',
      component: () => import('@/view/user')
    },
    {
      path: '/search',
      component: () => import('@/view/search')
    },
    {
      path: '/about',
      component: () => import('@/view/about')
    },
    {
      path: '/help',
      component: () => import('@/view/help')
    },
    {
      path: '/login',
      component: () => import('@/view/login')
    },
    {
      path: '/signup',
      component: () => import('@/view/signup')
    },
    {
      path: '/codelogin',
      component: () => import('@/view/codelogin')
    },
    {
      path: '/pwd',
      component: () => import('@/view/pwd')
    },
    {
      path: '/resetpwd',
      component: () => import('@/view/resetpwd')
    }
  ]
})



