import request from '@/utils/request'

export function editUserInfo(data) {
  return request({
    url:'/user/modifyinfo',
    method:'post',
    data
  })
}

export function getMyInfo() {
  return request({
    url:'/myspace/index',
    method:'post',
  })
}

export function getUserInfo(uid) {
  const data={
    uid
  }
  return request({
    url:'/myspace/friendindex',
    method:'post',
    data
  })
}
export function getFansList(page) {
  const data={
    page
  }
  return request({
    url:'/myspace/fanslist',
    method:'post',
    data
  })
}
export function getFollowList(page) {
  const data={
    page
  }
  return request({
    url:'/myspace/followlist',
    method:'post',
    data
  })
}
export function getMytheme(page) {
  const data={
    page
  }
  return request({
    url:'/mytopic/list',
    method:'post',
    data
  })
}

export function getMyspacetheme(ctype,page) {
  const data={
    ctype,
    page
  }
  return request({
    url:'/myspace/collectionlist',
    method:'post',
    data
  })
}

export function getMyspacecp(ctype,page) {
  const data={
    ctype,
    page
  }
  return request({
    url:'/myspace/collectionlist',
    method:'post',
    data
  })
}



