<template>
    <div class="scrollTop" v-show="showTop" @click="toTop">
        <div class="el-icon-arrow-up"></div>
    </div>
</template>


<script>
export default {
    name: "Totop",
    data() {
        return {
            scrollTop: 0,
            time: 0,
            dParams: 20,
            scrollState: 0
        }
    },
	computed:{
	      showTop: function(){
	        let value = this.scrollTop>200?true:false;
	        return value;
	      },
	    },
			  mounted() {
			        window.addEventListener('scroll', this.getScrollTop);
			      },
    methods: {
       toTop(e) {
               if(!!this.scrollState){
                 return;
               }
               this.scrollState = 1;
               e.preventDefault();
               let distance = document.documentElement.scrollTop || document.body.scrollTop;
               let _this = this;
               this.time = setInterval(function(){ _this.gotoTop(_this.scrollTop-_this.dParams) }, 10);
             },
             gotoTop(distance){
               this.dParams += 20;
               distance = distance>0 ? distance : 0;
               document.documentElement.scrollTop = document.body.scrollTop = window.pageYOffset = distance;
               if(this.scrollTop < 10){
                 clearInterval(this.time);
                 this.dParams = 20;
                 this.scrollState = 0;
               }
             },
             getScrollTop() {
               this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
             }
    }
}
</script>


<style scoped>
.load-more-wrapper {
    height: 100%;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    transition-duration: 300ms;
}
.load-more, .load-end {
        color: #aea699;
        font-size: .26rem;
        margin: .3rem 0;
        text-align: center;
    }
    .scrollTop{
      position: fixed;
      bottom: 100px;
      right: 100px;
      width: 50px;
      height: 50px;
      line-height: 50px;
      background: rgba(0,0,0,0.5);
      border-radius: 50px;
      text-align: center;
    }
   .scrollTop .el-icon-arrow-up{
     font-size: 20px;
     color: #fff;
   }
</style>



