<template>
  <li class="item" :class="size===1? 'xs':''" @click="navToDetail()">
    <a class="cover br5 bg-img" :class="size===1? 'xs':''" :style="{backgroundImage:'url('+ osshost+item.faceimg +')'}">
    </a>
    <div>
      <a class="name text-tips">{{item.cptitle}}</a>
      <div class="con-title text-lips_two" v-show="isDes===true">{{item.description}}</div>
      <a class="author">
        <img :src="osshost+item.authorImg" class="headicon br50">
        <span class="nickname text-lips" style="max-width: 238px">{{item.author}}</span>
      </a>
    </div>
  </li>
</template>

<script>
  export default {
    name: "foodItem",
    props: {
      isDes: Boolean,
      size: [String, Number],
      item: {
        type: Object,
        default: function (e) {
          return {};
        }
      }
    },
    data() {
      return {
        osshost: process.env.UPLOAD_URL,
      dtlist:'',
    }
    },
    mounted() {

    },
    methods: {
      navToDetail() {
        this.$router.push({path: "/cookBook", query: {id: 1}})
      },

    }

  }
</script>

<style scoped>

</style>



